
.services {
    padding: 60px 20px;
    text-align: center; 
    background-color: #f9f9f9; 
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); 
    margin: 20px auto; 
    max-width: 900px; 
}

.services h2 {
    font-size: 2.5rem; 
    margin-bottom: 40px;
    text-transform: uppercase; 
    letter-spacing: 1.5px;
    color: #024564; 
    position: relative; 
    transition: color 0.3s ease; 
}

.services h2:hover {
    color: #385d85; 
}

.service-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.service-item {
    background: #8294a8;
    color: #fff; 
    padding: 20px;
    border-radius: 8px;
    flex: 1 1 200px;
    min-width: 250px; 
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative; 
}


.service-item h3 {
    margin-bottom: 10px; 
    font-size: 1.5rem; 
    text-transform: uppercase; 
}


.service-item p {
    font-size: 0.9rem; 
}


.service-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}


@media (max-width: 768px) {
    .services h2 {
        font-size: 2rem; 
    }

    .service-item h3 {
        font-size: 1.2rem; 
    }
}

@media (max-width: 480px) {
    .services h2 {
        font-size: 1.5rem; 
    }

    .service-item h3 {
        font-size: 1rem; 
    }

    .service-item p {
        font-size: 0.8rem;
    }
}
