
.contact {
    padding: 60px 20px;
    text-align: center; 
    background-color: #f9f9f9; 
    border-radius: 10px; 
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    max-width: 800px; 
}

.contact h2 {
    font-size: 2.5rem;
    margin-bottom: 40px; 
    color: #024564; 
    text-transform: uppercase; 
    letter-spacing: 1.5px; 
}

.contact form {
    max-width: 600px; 
    margin: 0 auto;
    text-align: left; 
}

.contact input,
.contact textarea {
    width: 100%; 
    padding: 12px; 
    margin: 15px 0; 
    border: 1px solid #ccc; 
    border-radius: 6px;
    font-size: 1rem; 
    background-color: #fff; 
    transition: border-color 0.3s ease; 
}

.contact input:focus,
.contact textarea:focus {
    border-color: #30465e; 
}

.contact button {
    width: 100%; 
    padding: 12px; 
    background-color: #306eb1;
    color: #fff;
    border: none; 
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem; 
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact button:hover {
    background-color: #385d85; 
    transform: translateY(-3px); 
}

@media (max-width: 768px) {
    .contact h2 {
        font-size: 2rem; 
    }

    .contact form {
        max-width: 100%; 
    }
}
