
.hero {
    height: 100vh;
    display: flex;
    align-items: center; 
    justify-content: center; 
    color: white; 
    text-align: center;
    padding: 20px; 
    background-color: #007BFF;
}

.hero::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1; 
}

.hero-content {
    position: relative; 
    z-index: 2; 
}

.hero-content h1 {
    font-size: 3rem; 
    margin-bottom: 20px; 
}

.hero-content p {
    font-size: 1.2rem; 
    margin-bottom: 30px; 
}

.cta-button {
    padding: 15px 30px; 
    background-color: #FFD700; 
    color: #333;
    text-decoration: none; 
    border-radius: 50px; 
    transition: background-color 0.3s ease, color 0.3s ease; 
}

.cta-button:hover {
    background-color: #FFC300;
    color: white; 
}


@media (max-width: 768px) {
    .hero-content h1 {
        font-size: 2.5rem;
    }

    .hero-content p {
        font-size: 1rem; 
    }
}
