

.about {
    padding: 60px 20px; 
    background-color: #f9f9f9; 
    text-align: center; 
    border-radius: 10px; 
    box-shadow: 0 4px 20px rgba(32, 8, 8, 0.1); 
    margin: 20px auto; 
    max-width: 900px; 
    transition: background-color 0.3s ease; 
}

.about:hover {
    background-color: #7b99b8; 
}

.about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #024564; 
    text-transform: uppercase; 
    letter-spacing: 1.5px; 
    position: relative; 
    transition: color 0.3s ease; 
}

.about h2:hover {
    color: #385d85; 
}

.about h2::after {
    content: "";
    display: block; 
    width: 60px; 
    height: 3px; 
    background-color: #2dc7db;
    margin: 10px auto 0; 
    border-radius: 2px;
}

.about p {
    font-size: 1.1rem; 
    color: #555; 
    max-width: 800px; 
    margin: 0 auto; 
    line-height: 1.6;
    padding: 0 15px;
    transition: color 0.3s ease; 
}

.about p:hover {
    color: #007BFF; 
}

.about .highlight {
    color: #007BFF; 
    font-weight: bold; 
}


@media (max-width: 768px) {
    .about h2 {
        font-size: 2rem; 
    }

    .about p {
        font-size: 1rem; 
    }
}

@media (max-width: 480px) {
    .about h2 {
        font-size: 1.5rem; 
    }

    .about p {
        font-size: 0.9rem; 
    }
}
