.footer {
    background-color: #00799e;
    color: white; 
    padding: 20px 10%;
    text-align: center;
}

.footer-content {
    display: flex;
    flex-direction: column; 
    align-items: center; 
}

.footer-links {
    list-style: none; 
    display: flex; 
    padding: 0; 
    margin: 10px 0;
}

.footer-links li {
    margin: 0 15px; 
}

.footer-links a {
    text-decoration: none; 
    color: #FFD700; 
    transition: color 0.3s ease; 
}

.footer-links a:hover {
    color: #FFC300;
}


@media (max-width: 768px) {
    .footer-links {
        flex-direction: column; 
    }

    .footer-links li {
        margin: 10px 0; 
    }
}
